import React from "react"
import { Component } from "react"

import IconButton from "material-ui/IconButton"
import { withStyles } from "material-ui/styles"
import Toolbar from "material-ui/Toolbar"
import Table, { TableBody, TableCell, TableHead, TableRow } from "material-ui/Table"
import Paper from "material-ui/Paper"
import DeleteIcon from "material-ui-icons/Delete"
import TextField from "material-ui/TextField"
import { FormControl, FormHelperText } from "material-ui/Form"

const styles = theme => ({
  paper: {
    width: "100%",
    marginTop: theme.spacing.unit * 3,
    overflowX: "auto"
  },
  button: {
    margin: theme.spacing.unit
  }
})

let id = 0
function createData(name, calories, fat, carbs, protein) {
  id += 1
  return { id, name, calories, fat, carbs, protein }
}

const data = [
  createData("Frozen yoghurt", 159, 6.0, 24, 4.0),
  createData("Ice cream sandwich", 237, 9.0, 37, 4.3),
  createData("Eclair", 262, 16.0, 24, 6.0),
  createData("Cupcake", 305, 3.7, 67, 4.3),
  createData("Gingerbread", 356, 16.0, 49, 3.9)
]

const types = [{id: 2323, name: "boolean"}, {id: 232, name: "number"}]

class NewTypeForm extends Component {
  render() {
    const { classes, onSave } = this.props

    return (
      <Paper className={classes.paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell />
              <TableCell>Property Name</TableCell>
              <TableCell>Property Type</TableCell>
              <TableCell>Multiple Values?</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.map(n => {
              return (
                <TableRow key={n.id}>
                  <TableCell>
                    <IconButton color="default" aria-label="add" className={classes.button}>
                      <DeleteIcon />
                    </IconButton>
                  </TableCell>
                  <TableCell>
                    <TextField required label="" placeholder="" margin="normal" />
                  </TableCell>
                  <TableCell>
                  <TextField
                  id="select-currency-native"            
                  label="Native select"                  
                  value={232}
                  helperText="Please select your currency"
                  margin="normal"
                >
                  {types.map(option => (
                    <option key={option.id} value={option.id}>
                      {option.name}
                    </option>
                  ))}
                </TextField>
                  </TableCell>

                  <TableCell>
                    <TextField label="" placeholder="" margin="normal" />
                  </TableCell>
                </TableRow>
              )
            })}
          </TableBody>
        </Table>
      </Paper>
    )
  }
}
//   {["name", "type", "multiple"].map(key => {

export default withStyles(styles)(NewTypeForm)



